import './styles.css';
import template from './templates/template.hbs';
import listTemplate from './templates/listTemplate.hbs';
import fetchCountries from './fetchCountries';
import { alert, notice, info, success, error } from '@pnotify/core';

var debounce = require('debounce');

const inputEl = document.querySelector(".input");
const cardWrapperEl = document.querySelector(".card_wrapper");


inputEl.addEventListener('input', debounce(inputSearch, 500));


if(localStorage.getItem("value") !== "") {
   fetchCountries(`${localStorage.getItem("value")}`).then(data=>{
      inputEl.value = localStorage.getItem("value");
      console.log(data.length);
      if (data.length === 1) {
         cardWrapperEl.innerHTML = "";
         const murkup = template(data);
         cardWrapperEl.insertAdjacentHTML('beforeend', murkup)
            }
         if (data.length > 1 && data.length <= 10) {
            cardWrapperEl.innerHTML = "";
            const murkup = listTemplate(data);
            cardWrapperEl.insertAdjacentHTML('beforeend', murkup)
            }
         if (data.length > 10) {
            cardWrapperEl.innerHTML = "";
            const myInfo = info({
               text: "Зробіть запрос більш специфічним",
               addClass: 'brighttheme',
               delay:1000
             });  
            }
   })
   
}


function  inputSearch(e) {
   if (inputEl.value === "") {
      localStorage.setItem('value', "");
      cardWrapperEl.innerHTML = "";
      return myInfo = info({
         text: "Введіть назву країни",
         addClass: 'brighttheme',
         delay:1000
       });  
   }
   console.log(inputEl.value);
   localStorage.setItem('value', inputEl.value);
   fetchCountries(`${inputEl.value}`).then(data=>{
       console.log(data.length);

      if (data.length === 1) {
      cardWrapperEl.innerHTML = "";
      const murkup = template(data);
      cardWrapperEl.insertAdjacentHTML('beforeend', murkup)
         }
      if (data.length > 1 && data.length <= 10) {
         cardWrapperEl.innerHTML = "";
         const murkup = listTemplate(data);
         cardWrapperEl.insertAdjacentHTML('beforeend', murkup)
         }
      if (data.length > 10) {
         cardWrapperEl.innerHTML = "";
         const myInfo = info({
            text: "Зробіть запрос більш специфічним",
            addClass: 'brighttheme',
            delay:1000
          });  
         }
      }
   ); 
};
